import React, { Component } from 'react';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import './Feature.css';

class Feature extends Component {

    render() {
        return (
            <section id={this.props.id}>

                <Container>

                    <Row data-aos="fade-up" data-aos-duration="1500">

                        <Col md className="text-md-left mt-5 vertical-center">

                            <div>
                                <h1 className="display-1">{this.props.title}</h1>
                                <p className="lead">{this.props.content}</p>
                            </div>

                        </Col>

                        <Col md className="text-center align-middle">
                            {this.props.display}
                        </Col>

                    </Row>

                </Container>

            </section>
        );
    }

}

export default Feature;